height: 160px;
padding: 30px;
text-align: left;
}
.option p{
  margin-bottom: 10px;
}
.option p:nth-child(2){
  color: #a3a2a2;
}
.option button{
  background-color: transparent;
  border:none;
  text-decoration: underline;
  text-underline-position: under;
}
.p_m{
  transform: translate(2px, 67px);
}
.p_m button{
  width: 20px;
  height: 20px;
  border: .5px solid #9c9c9c;
  background-color: transparent;
}
.p_m p{
  display: inline-block;
  width: 30px;
  text-align: center;
}
.shipping{
  transform: translate(8px, 67px);
  
}
.t_btn{
  transform: translate(2px, 20px);
  width: 200px;
}
.t_btn button{
  width: 130px;
  height: 40px;
  display: block;
  border: .5px solid #9c9c9c;
  background-color: transparent;
  margin-top: 10px;
  padding: 10px;

}
.delet{
  width: 1300px;
  height: 50px;
  margin: 0 auto;
  text-align: left;
}
.delet button{
  width: 150px;
  height: 50px;
  background-color: transparent;
  border: .5px solid #9c9c9c;
  margin: 12px 10px 0 0 ;
  padding-top: 9px;
}
.all_price{
  width: 1300px;
  margin: 0 auto;
  padding: 100px 0;
}
.all_price table{
  width: 100%;

}
.all_price tr{
height: 90px;
border-bottom: .5px solid #9c9c9c;
}
.all_price tbody td:nth-child(even){
  border: 1px solid #222;
  display: inline-block;
  width: 20px;
  height: 20px;
  transform: translate(0, 40px);
  box-shadow: 1px 1px 1px #e0e0e0;
}
.all_price tbody .black{
  background-color: #222;
  color: #fff;
  border: none;
}
.all_price button{
  margin: 50px 10px;
  width: 150px;
  height: 50px;
  border: .5px solid #9c9c9c;
  background-color: transparent;
  padding-top: 8px;
}
.all_price .al-box{
  background-color: #464646;
  color: #fff;
}


@media(max-width:700px) {

  .icon {
    display: none;
  }

  .container {
    margin: 0;
  }

  h3 {
    padding-left: 30px;
  }

  h5,
  p {
    margin-left: 30px;
  }

  img {
    width: 100%;
    height: auto;
  }

  .logo {
    width: 70%;
  }

  .logo img {
    width: 150px;
  }

  .slider>img {
    height: 60vh;
  }

  .benner {
    flex-wrap: wrap;
  }

  .benner img {
    width: 100%;
  }

  .bg_t {
    width: 100%;
    height: auto;
    padding: 0;
  }

  .reco_box {
    height: auto;
    width: 100%;

  }

  .reco_img_box {
    margin: 0 auto;
    width: 100%;
    padding: 30px 0;
    text-align: center;

  }

  .reco-text {
    margin-left: 0;
  }

  .reco_img_box a {
    margin: 0 auto;
  }

  .reco-img-text {
    margin: 0 auto;
    width: 280px;

  }

  .swiper {
    width: 100%;
  }

  .swiper-slide img {
    width: 100%;
  }

  .last-benner {
    width: 100%;
    height: auto;
  }

  .last-benner img {
    width: 100%;
    height: auto;
  }

  .last-benner button {
    width: 80px;
    height: 30px;
    font-size: 12px;
  }

  .footer-box {
    width: 100%;
    height: auto;
    flex-wrap: wrap;
    margin-left: 10px;
  }

  .footer-text-box>img {
    width: 70%;
  }

  .none {
    display: none;
  }

  .footer-infor {
    width: 100%;
  }

  .infor-ul {
    width: 100%;
    margin-top: 30px;
  }

  .list-ul {
    width: 100%;

  }

  .list-ul>ul {
    justify-content: center;
  }

  .list-ul>ul>li {
    width: 60px;
  }

  .list-ul>ul>li:nth-child(even) {
    width: 10px;
  }

  .furni > .nav-link{
    text-align: center;
  }
 .furni-text{
  width: 100%;
 }
 .furni-text>h5,p{
  margin: 0;
 }



}

